
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box ;
}

html{
  background: #536872;
}

.App {
  width: 100vw;
  height: 100hw;
  max-height: 100vw;
  text-align: center;
  background-color: #536872;
  font-family: Arial;
/*font: 18px/1.66 ProIntro;*/
/*,Verdana,sans-serif;*/
  color: #153643;
  font-size: 13px;
}

header{
  background-color:#D9D9D9;
}

.toolbar, .setting-bar{
background-color:#D9D9D9;
display: flex;
flex-wrap: wrap;
align-items: center;
width: 100%;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.toolbar{
  background-color:#71A8AE;
  padding-bottom: 10px;
  padding-left: 12px;
  padding-right: 28px;
}

.setting-bar{
  margin-top: 7px;
  padding: 7px 15px 7px;
}

.setting-bar .setting{
  display: flex;
  align-items: center;

}

.toolbar__btn {
  width: 25px;
height: 25px;
border: none;
outline: none;
cursor: pointer;
background-size: cover;
margin-left: 16px;
margin-top: 10px;
}

.brush{
  background: url("assets/img/brush7.png") no-repeat center center;
    background-size: contain;
}

.rect{
  background: url("assets/img/rect2.png") no-repeat center center;
    background-size: contain;
}

.circle{
  background: url("assets/img/circle2.png") no-repeat center center;
    background-size: contain;
}

.eraser{
  background: url("assets/img/eraser2.png")   no-repeat center center;
    background-size: contain;
}

.eraser2{
  background: url("assets/img/eraser3.png")   no-repeat center center;
    background-size: contain;
}

.line{
  background: url("assets/img/line2.png") no-repeat center center;
}

#line-width , #stroke-color,
#fill-color{
  border:2px solid #B7B7B7;
  width:28px;
  border-radius: 3px;
  margin: 0px 10px;
}

.undo{
  background: url("assets/img/undo2.png") no-repeat center center;
  margin-left: auto ;
    background-size: contain;
}

.redo{
  background: url("assets/img/redo2.png") no-repeat center center;
    background-size: contain;
}

.save{
  background: url("assets/img/save3.png") no-repeat center center;
    background-size: contain;
}

.canvas {
  overflow: auto;
/*   height: 100vh;*/
  display: flex;
  justify-content: center;
  background-color: #536872;
  margin: 0 auto;
  padding:10px;
  margin-top: 20px;

}

canvas{
background-color: white;
/*border: 2px solid #062855;*/
box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);

}